"use client"

import { useState } from "react"
import LeftArrow from '../assets/images/arrow-left.svg'
import RightArrow from '../assets/images/arrow-right.svg'
import ImgHumar1 from '../assets/images/miners/diamond.png'
import ImgHumar2 from '../assets/images/miners/leaf.png'
import '../index.css'
import Referral from "./Referal"
import Inventory from "./Inventory"
// import Store from "./Store"
import Marketplace from "./MarketPlace"

const mockItems = [
    { id: 77, level: 1, type: "scythe", color: "green", selected: false },
    { id: 86, level: 1, type: "pickaxe", color: "orange", selected: false },
    null, null,
    { id: 12, level: 1, type: "pickaxe", color: "green", selected: false },
]

export default function SpacePanel() {
    const [activeTab, setActiveTab] = useState("Inventory")
    const [selectedItem, setSelectedItem] = useState(mockItems[0])

    const tabs = ["Inventory", "Marketplace", "Referral"]

    const getItemIcon = (item) => {
        if (!item) return null
        const src = item.type === "scythe" ? ImgHumar1 : ImgHumar2
        return (
            <div className="relative w-full h-full flex items-center justify-center">
                <img src={src} alt="" />
            </div>
        )
    }

    return (
        <div className="flex items-center justify-center">
            <div className="w-full max-w-5xl bg-slate-900/95 backdrop-blur-sm rounded-xl border-2 border-yellow-500 shadow-2xl shadow-yellow-500/20">
                {/* Tabs Header */}
                <div className="flex bg-[#1B2531] items-center rounded-l-xl rounded-r-xl justify-between lg:px-8 lg:py-10 py-4 pl-4 pr-2">
                    <div className="flex lg:space-x-12 space-x-6 overflow-x-auto hide-scrollbar">
                        {tabs.map((tab) => (
                            <button
                                key={tab}
                                onClick={() => setActiveTab(tab)}
                                className={`py-2 text-xl font-medium transition-all duration-200 relative ${activeTab === tab ? "text-yellow-400" : "text-slate-300 hover:text-slate-100"
                                    }`}
                            >
                                {tab}
                                {activeTab === tab && (
                                    <div className="absolute -bottom-3 left-0 right-0 h-0.5 bg-yellow-400 rounded-full"></div>
                                )}
                            </button>
                        ))}
                    </div>
                    <button className="text-yellow-400 hover:text-yellow-300 transition-colors p-1.5 border-2 border-yellow-500 rounded text-sm">
                        <svg className="lg:w-8 lg:h-8 w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                        </svg>
                    </button>
                </div>

                {/* Tab Content */}
                <div className="gap-8">
                    {/* {activeTab === "Store" && (
                        <Store />
                    )} */}

                    {activeTab === "Inventory" && (
                        <Inventory />
                    )}

                    {activeTab === "Marketplace" && (
                        <Marketplace />
                    )}

                    {activeTab === "Referral" && (
                        <Referral />
                    )}
                </div>
            </div>
        </div>
    )
}
