"use client"

import { useState } from "react"
import LeftArrow from '../assets/images/arrow-left.svg'
import RightArrow from '../assets/images/arrow-right.svg'
import ImgHumar1 from '../assets/images/miners/diamond.png'
import ImgHumar2 from '../assets/images/miners/leaf.png'
import '../index.css'
import ReferralCard from "./Referal"

const mockItems = [
  { id: 77, level: 1, type: "scythe", color: "green", selected: false },
  { id: 86, level: 1, type: "pickaxe", color: "orange", selected: false },
  null, null,
  { id: 12, level: 1, type: "pickaxe", color: "green", selected: false },
]

export default function Inventory() {
  const [activeTab, setActiveTab] = useState("Inventory")
  const [selectedItem, setSelectedItem] = useState(mockItems[0])

  const tabs = ["Inventory", "Marketplace", "Referral"]

  const getItemIcon = (item) => {
    if (!item) return null
    const src = item.type === "scythe" ? ImgHumar1 : ImgHumar2
    return (
      <div className="relative w-full h-full flex items-center justify-center">
        <img src={src} alt="" />
      </div>
    )
  }

  return (
    <div className="col-span-2 rounded-b-lg space-y-6 p-4 bg-[#1B2531]">
      <div className="flex justify-between">
        <div className="space-y-2 w-1/2 text-xl">
          <div className="text-yellow-400 font-medium">ID: #{selectedItem?.id ?? "‚Äî"}</div>
          <div className="text-yellow-400 font-medium">Level: {selectedItem?.level ?? "‚Äî"}</div>
          <div className="text-yellow-400 font-medium">Health: 0</div>
          <div className="text-yellow-400 font-medium">Power: 1000</div>
        </div>
        <div className="bg-black w-1/2 rounded-lg border border-slate-600 p-2 aspect-square max-w-28 max-h-28">
          <div className="relative w-full h-full">
            <div className="absolute top-1 left-1 text-xs text-yellow-400 font-bold">#{selectedItem?.id}</div>
            <div className="absolute top-1 right-1 text-xs text-white bg-slate-700/80 px-1.5 py-0.5 rounded">Lv {selectedItem?.level}</div>
            <div className="flex items-center justify-center h-full">{getItemIcon(selectedItem)}</div>
          </div>
        </div>
      </div>

      <div className="border-t border-slate-700/50"></div>

      <div className="space-y-3">
        <h3 className="text-white font-medium text-xl">Move Miner</h3>
        <div className="flex items-center space-x-4">
          <span className="text-white text-xl mr-3">A</span>
          <div className="flex items-center justify-center">
            <img src={LeftArrow} alt="" className="w-[25px]" />
            <img src={RightArrow} alt="" className="w-[25px]" />
          </div>
          <span className="text-white text-xl ml-3">B</span>
          <button className="px-3 py-1 text-xl text-white font-medium">SWAP</button>
        </div>
      </div>

      <div className="border-t border-slate-700/50"></div>

      <div className="space-y-3">
        <h3 className="text-yellow-400 font-medium text-xl">Sell Miner</h3>
        <div className="flex space-x-2">
          <div className="relative flex-1">
            <input
              type="text"
              placeholder="Price"
              className="w-full px-3 text-xl py-2 bg-[#15120D] border-2 border-yellow-500 rounded-lg text-white placeholder-slate-400 focus:outline-none focus:border-yellow-400 pr-12"
            />
            <span className="absolute right-3 text-xl top-1/2 transform -translate-y-1/2 text-yellow-400 font-medium">ETH</span>
          </div>
          <button className="px-4 py-2 text-yellow-500 border-2 border-yellow-500 rounded-lg text-sm font-medium">LIST</button>
        </div>
      </div>

      <div className="grid grid-cols-5 gap-3 mt-6">
        {mockItems.map((item, index) => (
          <div
            key={index}
            onClick={() => item && setSelectedItem(item)}
            className={`relative aspect-square flex items-center justify-center bg-black rounded-lg border cursor-pointer transition-all hover:border-slate-500 ${item ? "border-slate-600" : "border-slate-700/50"
              } ${item && item.selected ? "border-yellow-400 shadow-lg shadow-yellow-400/20" : ""}`}
          >
            {item ? (
              <>
                <div className="absolute top-1 left-1 text-xs text-white font-bold">#{item.id}</div>
                <div className="p-2 h-full">{getItemIcon(item)}</div>
              </>
            ) : (
              <h2 className="text-slate-600 pb-[7px] lg:pb-0 h-fit lg:text-6xl mb-0 text-[26px] font-black">+</h2>
            )}
          </div>
        ))}
      </div>
    </div>
  )
}
