import React from "react";

export default function Referral() {
  const referralLink = "https://hashlordz.com?ref=0x84ac2502626a85d2bdf210bfab7e4513d1fec16";

  const handleCopy = () => {
    navigator.clipboard.writeText(referralLink);
    // alert("Referral link copied!");
  };

  return (
    <div className="p-8 text-white">
      <div className="max-w-md mx-auto  font-mono">
        <h2 className="text-center text-lg font-bold border-b border-white pb-2 mb-4">
          REFER A FRIEND
        </h2>

        <p className="text-center text-sm mb-4">
          SHARE YOUR REFERRAL LINK WITH FRIENDS AND <br />
          EARN A <span className="text-yellow-400">2.5%</span> BONUS OF WHATEVER HASHCOIN THEY MINE!
        </p>

        <div className="flex justify-between items-center bg-[#122533] p-4 rounded mb-4">
          <div className="text-center">
            <p className="text-sm text-gray-400">TOTAL REFERRALS</p>
            <p className="text-xl font-bold text-yellow-400">0</p>
          </div>
          <div className="text-center">
            <p className="text-sm text-gray-400">TOTAL BIG EARNED</p>
            <p className="text-xl font-bold text-yellow-400">0.00 $BIG</p>
          </div>
        </div>

        <div className="bg-[#122533] p-3 rounded flex items-center justify-between mb-4">
          <span className="text-xs break-all">{referralLink}</span>
          <button
            onClick={handleCopy}
            className="bg-yellow-400 text-black text-xs px-3 py-1 ml-2 rounded hover:bg-yellow-300 font-bold"
          >
            COPY
          </button>
        </div>

        <button className="bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 font-bold w-full py-1 text-[#78350F] text-[18px] px-6 rounded-md">
          CLOSE
        </button>
      </div>
    </div>
  );
}
